<template>
    <div style="margin: 20px 0;text-align: left;">
        <!-- 检查中禁止点击 -->
        <button style="padding: 5px 20px; margin-left: 200px;" @click="checkAll" :disabled="loading">
            {{ loading ? '处理中...' : '手动检查' }}
        </button>
    </div>

    <div style="height: 200px;">
        <h3 style="text-align: left;padding-left: 30px;">测试地点: 广州</h3>

        <div class="tr" v-for="(value, key, index) in domains" :key="index">
            <div class="td">{{ value[0] }}</div>
            <div class="td">{{ key }}</div>

            <div class="td">
                <label>状态:
                    <span :class="getStatusClass(value[1])">{{ value[1] }}</span>
                </label>
            </div>
        </div>
    </div>
    <div style="clear: both;"></div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    data() {
        return {
            // 服务器地址
            baseUrl: 'https://check.wayson.work:7890/check/',
            loading: false,
        };
    },
    computed: {
        ...mapGetters(['domains']),
    },
    methods: {
        async checkAll() {
            this.loading = true;
            const urls = Object.keys(this.domains).map(async key => {
                const url = `${this.baseUrl}http?url=https://${key}`;
                this.domains[key][1] = "处理中";
                return fetch(url)
                    .then(response => {
                        if (!response.ok) {
                            console.error('响应状态:', response.status); // 添加响应状态调试
                            throw new Error('网络响应不是 OK');
                        }
                        return response.json();
                    })
                    .then(data => {
                        if (data.error) {
                            console.error('API 错误:', data.error); // 添加 API 错误调试
                            throw new Error(data.error);
                        } else {
                            this.domains[key][1] = "成功";
                            setTimeout(() => {
                                this.domains[key][1] = "待检查";
                            }, 9600000);
                        }
                    })
                    .catch(err => {
                        this.domains[key][1] = "失败";
                        console.error('请求数据错误:', err);
                        console.error('请求 URL:', url);
                    });
            });
            await Promise.all(urls);
            this.loading = false;
        },
        getStatusClass(status) {
            if (status === "成功") {
                return "success";
            } else if (status === "失败") {
                return "error";
            } else {
                return "pending";
            }
        }
    }
}
</script>


<style scoped>
.tr {
    float: left;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
}

.tr div {
    display: flex;
    padding: 5px 10px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    overflow: hidden;
}

.td {
    margin: 2px 0;
}

.td:nth-child(1) {
    border-left: 1px solid black;
    width: 120px;
    margin-left: 3px;
    font-weight: 700;
}


.td:nth-child(2) {
    justify-content: start;
    width: 180px;
}


.td:nth-child(3) {
    border-right: 1px solid black;
    font-size: 14px;
    font-weight: 900;

}

.tr label {
    margin-right: 10px;
}

.success {
    color: green;
}

.error {
    color: red;
}

.pending {
    color: orange;
}
</style>
