<template>
  <div id="logo">
    <div style="text-align: center;">
      <button id="clear" @click="clearCache">清除缓存</button>
    </div>
  </div>
  <Check></Check>
  <HuaShu></HuaShu>
</template>

<script>
import HuaShu from '@/views/HuaShu.vue'
import Check from '@/views/AboutView.vue'

export default {
  name: 'App',
  components: {
    HuaShu,
    Check
  },
  methods: {
    clearCache() {
      if (localStorage) {
        localStorage.clear();
      }
      if (sessionStorage) {
        sessionStorage.clear();
      }
      console.log('缓存已清除');
    },
  }
}
</script>

